.news{
  display: block;
  position: relative;
  float: left;
  width: 100%;
  background-color: #fff;
  &__wrapper{
    @include display-flex(flex);
    @include flex-wrap(wrap);
    @include flex-direction(row);
    @include justify-content(space-between);
    position: relative;
    float: left;
    width: 100%;
    padding-bottom: 71px;
  }
  &__element{
    position: relative;
    float: left;
    width: calc(25% - 7px);
    background-color: #ffffff;
    margin-bottom: 30px;
    &--photo{
      position: relative;
      float: left;
      width: 100%;
      height: 171px;
      overflow: hidden;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      img{
        size: 100%;
        object-fit: cover;
        @include transition(0.4s);
      }
    }
    &--box{
      position: relative;
      float: left;
      width: 100%;
      box-shadow: 0 1px 11px rgba(52,73,94,0.4);
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      padding-top: 29px;
      padding-bottom: 25px;
      padding-left: 20px;
      padding-right: 20px;
      @include box-sizing(border-box);
    }
    &--title{
      display: block;
      display: -webkit-box;
      position: relative;
      float: left;
      width: 100%;
      height: calc(15px*1.3*2);
      height: -moz-calc(15px*1.3*2);
      margin-top: 0;
      margin-bottom: 0;
      color: #000000;
      font-size: 15px;
      line-height: 1.3;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    &--descr{
      display: block;
      display: -webkit-box;
      position: relative;
      float: left;
      width: 100%;
      height: calc(14px*1.3*2);
      height: -moz-calc(14px*1.3*2);
      margin-top: 10px;
      margin-bottom: 0;
      color: #000000;
      font-size: 14px;
      line-height: 1.3;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  &__element:hover{
    .news__element--photo img{
        @include transition(0.4s);
        transform: scale(1.2) rotate(5deg);
    }
  }
}